let userLang = localStorage.getItem('lang') !== null ? localStorage.getItem('lang') : '';
if (userLang === '') {
    if (["zh-HK", "zh-TW", "zh-CN", "zh-Hans", "zh-Hant"].indexOf(navigator.language) !== -1) {
        userLang = "zh-HK";
    } else {
        userLang = resources[navigator.language] !== undefined ? navigator.language : "en";
    }
}

i18n.use(initReactI18next)
    .init({
        resources,
        lng: userLang,
        interpolation: {
            escapeValue: false
        }
    });

export default i18n;